import React from 'react';

const TetSection = () => {
  const tetItems = [
    {
      image:
        'https://f1e425bd6cd9ac6.cmccloud.com.vn/cms-tool/post/images/313/img_card.png?v=27',
      title: 'Vé tàu Tết 2025',
      description: 'Lịch mở bán, giá vé tàu Tết Ất Tỵ',
    },
    {
      image:
        'https://f1e425bd6cd9ac6.cmccloud.com.vn/cms-tool/post/images/314/img_card.png?v=11',
      title: 'Vé máy bay Tết 2025',
      description: 'Lịch mở bán, giá vé máy bay Tết Ất Tỵ',
    },
    {
      image:
        'https://f1e425bd6cd9ac6.cmccloud.com.vn/cms-tool/post/images/316/img_card.png',
      title: 'Lịch nghỉ Tết 2025',
      description: 'Thông báo lịch nghỉ Tết Dương lịch, Âm lịch 2025',
    },
    // Bạn có thể thêm nhiều mục khác nếu cần
  ];

  return (
    <section className="tet-section mt-12 px-4 md:px-6">
      <h2 className="text-3xl font-extrabold text-center text-red-600 mb-12">
        Tết 2025
      </h2>
      <div className="relative">
        <div className="flex overflow-x-auto md:grid md:grid-cols-3 md:gap-8 space-x-6 md:space-x-0 pb-4 hide-scroll-bar">
          {tetItems.map((item, index) => (
            <div
              key={index}
              className="flex-none w-72 md:w-auto bg-gradient-to-br from-yellow-400 to-red-500 rounded-xl shadow-xl overflow-hidden transform hover:scale-105 transition duration-300 cursor-pointer"
            >
              <img
                src={item.image}
                alt={item.title}
                className="w-full h-64 object-cover opacity-90"
              />
              <div className="p-6">
                <h3 className="text-2xl font-bold text-white mb-2">
                  {item.title}
                </h3>
                <p className="text-white">{item.description}</p>
              </div>
            </div>
          ))}
        </div>

        {/* Left Gradient */}
        <div className="absolute left-0 top-0 h-full w-8 bg-gradient-to-r from-white to-transparent pointer-events-none md:hidden"></div>
        {/* Right Gradient */}
        <div className="absolute right-0 top-0 h-full w-8 bg-gradient-to-l from-white to-transparent pointer-events-none md:hidden"></div>
      </div>
    </section>
  );
};

export default TetSection;
